@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
:root {
  --ff-primary-regular: "Poppins", sans-serif;
  --font-large: 3rem;
  --font-med: 2.25rem;
  --font-small: 1.25rem;
  --font-xsmall: 1rem;
}

:root {
  --c-grey-000: #ffffff;
  --c-grey-100: #f7f5ff;
  --c-grey-500: #dddddd;
  --c-grey-700: #7d7d80;
  --c-grey-900: #000000;
  --c-purple-100: #cac2f2;
  --c-purple-300: #9684fe;
  --c-purple-500: #7762ff;
  --inner-gutter: 1.25rem;
  --first-background-color: #cbd7e3;
  --second-background-color: #c0ccd8;
  --third-background-color: #4d4bc8;
  --fourth-background-color: #dee3e6;
  --fifth-background-color: #b0a5ea;
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  overflow-x: hidden;
}

body {
  scroll-behavior: smooth;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

a {
  color: inherit;
}

input,
button,
textarea,
select {
  font: inherit;
}

button {
  border: 1px solid;
  background-color: transparent;
}

button {
  -moz-appearance: none;
  appearance: none;
  -webkit-appearance: none;
}

h1 {
  margin-top: 0px;
  margin-bottom: 0px;
  font-size: 40px;
  font-weight: 900;
}

h2 {
  margin-top: 0px;
  margin-bottom: 12px;
  font-size: 15px;
  font-weight: 900;
}

h3 {
  margin-top: 0px;
  margin-bottom: 16px;
  font-size: 20px;
  line-height: 38px;
  font-weight: 300;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

::-webkit-scrollbar {
  display: none;
}

* {
  scrollbar-width: none;
}

body {
  font-family: "Poppins", sans-serif;
  min-height: 100vh;
  background-color: var(--first-background-color);
  padding: 0.5rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (max-width: 39.9375em) {
  body {
    max-width: 100%;
    padding: 0;
  }
}

img {
  max-width: 100%;
  display: block;
}

.glassmorphism-card {
  background: rgba(255, 255, 255, 0.05);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(16.5px);
}

input:focus {
  font-size: 16px;
}

.welcome-page__container {
  position: fixed;
  inset: 0;
  z-index: 100;
  width: 100%;
  overflow: hidden;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 3rem 1rem;
  transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transform-origin: bottom left;
  background-color: var(--c-grey-100);
}
@media (min-width: 40em) {
  .welcome-page__container {
    margin-inline: auto;
    max-width: 375px;
    border-radius: 24px;
    position: relative;
  }
}
.welcome-page__image-container {
  height: 40vh;
  width: 90%;
}
.welcome-page__title {
  text-align: center;
  color: var(--black);
  padding: 2rem 0;
}
.welcome-page__subtitle {
  text-align: center;
  color: var(--black);
}
.welcome-page__second-subtitle {
  text-align: center;
  opacity: 0.4;
}
.welcome-page__btn {
  padding: 1.3em 3em;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  font-weight: 500;
  background-color: var(--c-purple-300);
  color: var(--first-background-color);
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 45px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
}

.items-container {
  position: fixed;
  inset: 0;
  z-index: 90;
  width: 100%;
  min-height: 100vh;
  overflow: scroll;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  transform: translateX(200%);
  transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transform-origin: bottom left;
  background-color: var(--c-grey-100);
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.1);
  position: relative;
}
@media (min-width: 40em) {
  .items-container {
    margin-inline: auto;
    max-width: 375px;
    border-radius: 24px;
    position: relative;
  }
}
.items-container__greetings, .items-container__title {
  display: flex;
}
.items-container__title input {
  margin-left: 0.5rem;
  flex: 1 1 0%;
  min-width: 0;
  appearance: none;
  border: none;
  outline: none;
  background: none;
}
.items-container__title, .items-container__title input,
.items-container .your-schedule {
  color: var(--c-grey-900);
  font-size: 1.5rem;
  font-weight: 500;
}
.items-container__current-date {
  display: flex;
  justify-content: space-between;
  border-radius: 30px;
  padding: 0 0.5rem;
}
.items-container__current-date .text-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-left: 1rem;
}
.items-container__current-date .text-wrapper p {
  font-size: 1rem;
}
.items-container__current-date .image-wrapper {
  height: 15vh;
  width: 100%;
}
.items-container__category-container {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: auto;
}
.items-container__category-container .title,
.items-container__category-container .subtitle,
.items-container__category-container .category-counter {
  text-align: center;
  font-size: 1.2rem;
}
.items-container__category-container .subtitle {
  font-size: 0.9rem;
  padding-bottom: 3rem;
}
.items-container__category-container .title,
.items-container__category-container .subtitle {
  opacity: 0.7;
}
.items-container__category-container .round-plus {
  position: relative;
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--c-purple-300);
  color: var(--white);
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.3);
  align-self: center;
  margin-top: 2rem;
}
.items-container__category-container .round-plus i {
  color: var(--first-background-color);
  line-height: 60px;
  font-size: 1.5rem;
}

.overflow-hidden {
  overflow: hidden;
  overflow-x: hidden;
}

/* Modal */
.modal-radio {
  position: absolute;
  visibility: hidden;
  opacity: 0;
}

.modal {
  position: fixed;
  height: 300vh;
  width: 100%;
  inset: 0;
  right: auto;
  bottom: auto;
  overflow: hidden;
  opacity: 0;
  transform: scale(0);
  transition: opacity 0.25s, transform 0.25s;
  z-index: 99;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 3rem;
  overflow: hidden;
}
.modal .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  inset: 0;
}
.modal .content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 90%;
  max-height: 90vh;
  background: white;
  backdrop-filter: blur(10px);
  border-radius: 15px;
  box-shadow: 0 3px 15px -2px #222;
  padding: 4rem 2rem;
}
.modal .content .top {
  margin-bottom: 2rem;
}
.modal .fa-circle-xmark {
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 2.5rem;
}
.modal form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.modal form .input-field {
  position: relative;
  width: 100%;
}
.modal form .input-field label {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  color: var(--c-grey-900);
  opacity: 0.5;
  transition: 0.2s all;
  cursor: text;
}
.modal form .input-field input {
  width: 100%;
  border: 0;
  outline: 0;
  padding: 0.5rem 0;
  background-color: transparent;
  border-bottom: 2px solid var(--c-grey-900);
  box-shadow: none;
  color: var(--c-grey-900);
}
.modal form .input-field input:invalid {
  outline: 0;
}
.modal form .input-field input:focus {
  font-size: 16px;
}
.modal form .input-field input:focus ~ label,
.modal form .input-field input:valid ~ label {
  font-size: 14px;
  top: -24px;
}
.modal form input[type=submit] {
  min-height: 2rem;
  margin-top: 1rem;
  padding: 1.3em 3em;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  font-weight: 500;
  color: var(--c-grey-900);
  background-color: transparent;
  border: 1px solid var(--c-grey-900);
  border-radius: 45px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  outline: none;
}
.modal form input[type=submit]:hover {
  min-height: 2rem;
}

#modal-open:checked + .modal {
  opacity: 1;
  transform: scale(1);
}

.category-box-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  min-height: 2rem;
  margin-top: 2rem;
  z-index: 10;
}

.category-wrapper {
  display: flex;
  min-height: 25vh;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
  transition: all 0.5s ease;
}

.category-box {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 15px;
  min-height: 15vh;
  height: auto;
  transition: height 1s ease;
  cursor: pointer;
  touch-action: pan-y;
}
.category-box__task {
  padding-bottom: 1rem;
}
.category-box__name {
  border: none;
  outline: none;
  font-size: 1.3rem;
  background-color: transparent;
  text-align: center;
  padding-top: 1rem;
  text-transform: uppercase;
}

.btn-container {
  display: flex;
  justify-content: space-between;
  padding-right: 1rem;
  margin: 1rem 0;
}

.button-open {
  margin-top: 0.3rem;
  padding: 1em 3em;
  font-size: 9px;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  font-weight: 500;
  color: var(--c-grey-500);
  background-color: var(--c-purple-300);
  border-radius: 45px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  width: 100%;
}

.edit-btn,
.delete-btn {
  cursor: pointer;
  margin: 0 0.5rem;
  padding: 0 1.5rem;
  color: var(--c-grey-900);
  font-size: 1rem;
  border-radius: 45px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(2px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-right-color: rgba(255, 255, 255, 0.1);
  border-bottom-color: rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 30px rgba(0, 0, 0, 0.1);
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.fall {
  transform: translateY(8rem) rotateZ(20deg);
  opacity: 0;
}

.tasks-counter {
  margin: 0;
  font-size: 0.8rem;
  width: 100%;
  text-align: center;
  padding-bottom: 1rem;
}

.active {
  position: fixed;
  inset: 0;
  z-index: 2000;
}

/* Items/Task To Do Container/Input Form - Create Single Item */
.task-elements-container {
  display: none;
  width: 100%;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-top: 1rem;
  border-radius: 50px 50px 0 0;
  min-height: 100vh;
  margin-top: 2rem;
  padding-left: 1rem;
  padding-right: 1rem;
  background: rgba(255, 255, 255, 0.05);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(16.5px);
}

.task-input {
  margin: 0;
  padding: 0.2rem 1rem;
  font-size: 1rem;
  line-height: 1.8rem;
  border: none;
  outline: none;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  border-radius: 3px;
  font-size: 15px;
  padding-left: 30px;
  transition: all 0.3s ease;
  background-color: rgba(0, 0, 0, 0.21);
  backdrop-filter: blur(15px);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-left: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.2);
  border-radius: 50px;
}

.task-input:focus {
  background-color: rgba(255, 255, 255, 0.1);
}

.task-input::placeholder {
  color: var(--black);
}

.task-input:focus {
  outline: none;
}

.new-task-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 4rem;
}

.create-task-btn {
  min-height: 35px;
  width: 50%;
  align-self: center;
  font-size: 12px;
  background-color: var(--c-purple-300);
  color: var(--c-grey-500);
  margin: 1rem 0;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  font-weight: 500;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 45px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease 0s;
}

.tasks-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.task-container {
  margin-top: 1rem;
  width: 100%;
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.task-item {
  width: 100%;
  padding: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
  outline: none;
  border-radius: 3px;
  font-size: 17px;
  background-color: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(15px);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-left: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.2);
  background-color: var(--first-background-color);
}

.task-first {
  display: flex;
  align-items: center;
  width: 100%;
}

.completed-checkbox {
  appearance: none;
  position: relative;
  height: 20px;
  width: 20px;
  min-width: 20px;
  border: 1px solid var(--c-grey-900);
  border-radius: 50%;
  margin-right: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.completed-checkbox:checked {
  border: none;
}

.completed-checkbox:checked + p {
  color: #626567;
  text-decoration: line-through;
  opacity: 0.5;
  transition: all 0.3s ease;
}

.completed-checkbox:checked:after {
  content: "âœ”";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: var(--c-grey-900);
  font-size: 1.5rem;
}

.completed-checkbox:checked:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border-radius: 50%;
  background-color: var(--white);
}

.todo-text {
  color: var(--c-grey-900);
  border: none;
  background-color: transparent;
}

.todo-text:focus,
.todo-text:active {
  outline: none;
}

.todo-text.completed {
  text-decoration: line-through;
  opacity: 0.5;
}

.completed {
  color: #626567;
  text-decoration: line-through;
  opacity: 0.5;
}

.timer-container {
  margin: 0 auto;
  background-color: rgba(255, 255, 255, 0.06);
  -webkit-backdrop-filter: blur(90px);
  backdrop-filter: blur(90px);
  padding: 0 1rem;
  box-shadow: 0 25px 23px rgba(0, 0, 0, 0.15);
  border: 1.5px solid rgba(255, 255, 255, 0.06);
  border-radius: 8px;
  display: grid;
  grid-template-columns: repeat(3, 2fr, 1fr, 2fr);
  grid-template-rows: repeat(2, 1fr);
  justify-items: center;
  align-items: center;
  margin-right: 0;
  position: relative;
}

.timer__part {
  font-size: 1.2rem;
  padding: 0.5rem 0;
}

.timer__part--minutes {
  grid-area: 1/1/2/2;
}

.timer__part--semicolons {
  grid-area: 1/3/2/2;
}

.timer__btn {
  width: 60px;
  height: 30px;
  border: none;
  border-radius: 15px;
  background-color: var(--c-purple-300);
  color: var(--fourth-background-color);
  cursor: pointer;
  transition: all 1s ease;
  font-size: 0.8rem;
}

.timer__start {
  grid-area: 2/1/2/2;
}

.timer__setup {
  grid-area: 2/3/2/4;
}

.timer__btn--start {
  background-color: var(--c-purple-300);
}

.timer__btn--stop {
  background: #00b84c;
}

/* Progress Bar */
.progress-bar {
  position: relative;
  width: 95%;
  min-height: 15px;
  height: 15px;
  border-radius: 45px;
  background: #ccc;
  box-shadow: inset 0 -1px 1px rgba(255, 255, 255, 0.3);
  display: none;
  padding: 5px 8px;
  z-index: -2;
}

.progress-bar-fill {
  width: 100%;
  height: 100%;
  background: var(--c-purple-300);
  transition: width 0.5s;
  border-radius: 8px;
  display: block;
  background-image: linear-gradient(to top, rgb(43, 194, 83) 37%, rgb(84, 240, 84) 69%);
  box-shadow: inset 0 2px 9px rgba(255, 255, 255, 0.3) inset 0 -2px 6px rgba(0, 0, 0, 0.4);
  position: relative;
  overflow: hidden;
  transition: width 2s ease-out;
  z-index: -1;
}

.progress-bar-value {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  font-weight: 500;
}

#notification,
.notification-time-box {
  position: fixed;
  inset: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
  border-radius: 35px;
  z-index: 1000;
}
#notification .notification-box,
#notification .set-time-box,
.notification-time-box .notification-box,
.notification-time-box .set-time-box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 1rem 1.3rem;
  backdrop-filter: blur(10px);
  border-radius: 15px;
  box-shadow: 0 3px 15px -2px #222;
  width: 350px;
  overflow: hidden;
}
#notification .notification-box__content,
#notification .set-time-box__content,
.notification-time-box .notification-box__content,
.notification-time-box .set-time-box__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
#notification .notification-box__content .title,
#notification .set-time-box__content .title,
.notification-time-box .notification-box__content .title,
.notification-time-box .set-time-box__content .title {
  font-size: 1rem;
}
#notification .notification-box__content .time-title,
#notification .set-time-box__content .time-title,
.notification-time-box .notification-box__content .time-title,
.notification-time-box .set-time-box__content .time-title {
  font-size: 0.9rem;
  text-align: center;
}
#notification .notification-box__content .btn-close-box,
#notification .set-time-box__content .btn-close-box,
.notification-time-box .notification-box__content .btn-close-box,
.notification-time-box .set-time-box__content .btn-close-box {
  margin-left: 16rem;
  margin-bottom: 1rem;
  background: transparent;
  border: none;
}
#notification .notification-box__content .btn-close-box .fa-circle-xmark,
#notification .set-time-box__content .btn-close-box .fa-circle-xmark,
.notification-time-box .notification-box__content .btn-close-box .fa-circle-xmark,
.notification-time-box .set-time-box__content .btn-close-box .fa-circle-xmark {
  font-size: 1.7rem;
  cursor: pointer;
}
#notification .notification-box__btn-wrapper, #notification .notification-box__input-wrapper,
#notification .set-time-box__btn-wrapper,
#notification .set-time-box__input-wrapper,
.notification-time-box .notification-box__btn-wrapper,
.notification-time-box .notification-box__input-wrapper,
.notification-time-box .set-time-box__btn-wrapper,
.notification-time-box .set-time-box__input-wrapper {
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin: 15px 0 0 0;
}
#notification .notification-box__btn-wrapper input, #notification .notification-box__input-wrapper input,
#notification .set-time-box__btn-wrapper input,
#notification .set-time-box__input-wrapper input,
.notification-time-box .notification-box__btn-wrapper input,
.notification-time-box .notification-box__input-wrapper input,
.notification-time-box .set-time-box__btn-wrapper input,
.notification-time-box .set-time-box__input-wrapper input {
  padding: 1.3em 2em;
  font-size: 10px;
  text-transform: uppercase;
